.container {
    text-align: center;
}

.title {
    margin-top: 50px;
    color: white;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: moveUp 2s forwards;
}

.title span {
    color: #26ffcb;
    padding-left: 15px;
}

.reset, .clear-scores {
    width: 250px;
    height: 100px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 50px;
    background: #1f3540;
    font-size: 26px;
    color: #26ffcb;
    margin-top: 25px;
    margin-bottom: 50px;
}

.clear-scores {
    background: hsl(0, 65%, 41%);
    margin-top: 15px;
}

.boxes {
    display: flex;
    height: 180px;
    width: 180px;
    background: #0f3540;
    border: 4px solid #0f1b21;
    border-radius: 12px;
    cursor: pointer;
}

.board {
    height: 600px;
    width: 564px;
    display: flex;
    margin: auto;
    flex-wrap: wrap;
}

.boxes img {
    margin: 50px;
}

.scoreboard {
    margin-top: 20px;
    margin-bottom: 20px;
    color: white;
    font-size: 24px;
}

.scoreboard p {
    margin: 10px 0;
}

.player-selection {
    margin-top: 50px;
    color: white;
    opacity: 0; /* Initially hidden */
    animation: fadeIn 1s forwards; /* Fade-in animation */
    animation-delay: 2s; /* Starts after the title animation */
}

.player-selection h2 {
    margin-bottom: 20px;
    font-size: 36px;
    opacity: 0;
    animation: popUp 1s forwards;
    animation-delay: 3s; /* Delayed to sync with title */
}

.player-selection button {
    width: 300px;
    height: 100px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 50px;
    background: #1f3540;
    font-size: 24px;
    color: #26ffcb;
    margin: 15px;
    opacity: 0;
    animation: popUp 1s forwards;
    animation-delay: 3.5s; /* Sequential delay */
}

.player-selection button:nth-child(2) {
    animation-delay: 4s; /* Further delay for the second button */
}

@keyframes moveUp {
    0% {
        transform: translateY(300px) scale(3);
        opacity: 0;
    }
    50% {
        transform: translateY(150px) scale(1.5);
        opacity: 0.5;
    }
    100% {
        transform: translateY(0) scale(1);
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes popUp {
    0% {
        transform: scale(0.5);
        opacity: 0;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}
